<template>

  <v-layout row>
    <v-flex xs12>
      <v-card>
        <v-select v-model="filter" label="Фильтр устройств" chips tags solo prepend-icon="filter_list" append-icon="" clearable>
          <template slot="selection" slot-scope="data">
            <v-chip :selected="data.selected" close @input="remove(data.item)">
              <strong>{{ data.item.name }}</strong>&nbsp;
              <span>({{data.item.type}})</span>
            </v-chip>
          </template>
        </v-select>
        <v-container text-xs-center grid-list-sm>
          <v-layout wrap>
            <v-flex xs12 sm6 md4 lg3 v-for="item in selectedItems" :key="item.dev_id">
              <v-card hover tile :to="'/deviceinfo/'+item.dev_id">
                <v-container fluid flat pa-1 ma-1>
                  <v-layout row flat wrap align-center justify-center>
                    <v-flex xs2 ver>
                      <v-tooltip bottom>
                        <v-icon slot="activator">{{imgType(item.product_group1_name)}}</v-icon>
                        <span>{{item.product_category_name}}</span>
                      </v-tooltip>
                      <v-tooltip bottom>
                        <v-icon slot="activator" color="primary">{{imgState(item.state)}}</v-icon>
                        <span>{{item.dev_state}}</span>
                      </v-tooltip>
                    </v-flex>
                    <v-flex xs10 inline-flex pa-0 ma-0>
                      <v-card-text flat pa-0 ma-0>
                        <span>{{ item.product_name}} </span>
                        <span>({{ item.dev_id}})</span>
                      </v-card-text>
                    </v-flex>
                  </v-layout>
                </v-container>
              </v-card>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card>
    </v-flex>
  </v-layout>

</template>

<script>
  import DevList from '@/assets/data/device_list.json'
  export default {
    props: ['state', 'category'],
    watch: {
      state: function (newVal, oldVal) { // watch it
        console.log('Prop changed: ', newVal, ' | was: ', oldVal)
      },
      category: function (newVal, oldVal) { // watch it
        console.log('Prop changed: ', newVal, ' | was: ', oldVal)
      }
    },
    data: function () {
      let res = {
        filterOn: true,
        filter: [],
        items: DevList
      }
      if (this.state) res.filter.push({name: this.state, type: 'статус', param: 'state'})
      if (this.category) res.filter.push({name: this.category, type: 'категория', param: 'category'})
      return res
    },
    computed: {
      selectedItems: {
        get () {
          return this.items.filter(i => (!this.state || i.state === this.state) && (!this.category || i.product_group1_name === this.category))
        }

      }
    },
    methods: {
      remove (item) {
        this.filter.splice(this.filter.indexOf(item), 1)
        let param = []
        this.filter.forEach(function (i) { param[i.param] = i.name })
        this.$router.push({name: 'DeviceList', params: param})
      },
      imgState (state) {
        let ret = {
          'InvalidPrice': 'report',
          'HasIssues': 'warning',
          'NotYetConneced': 'sync',
          'New': 'plus_circle',
          'Replaced': 'swap_vert',
          'Connected': 'star',
          'Removed': 'minus_circle'
        }[state]
        if (!ret) return ''
        return ret
      },
      colorState (state) {
        let ret = {
          'InvalidPrice': 'report',
          'HasIssues': 'warning',
          'NotYetConneced': 'sync',
          'New': 'plus_circle',
          'Replaced': 'swap_vert',
          'Connected': 'star',
          'Removed': 'minus_circle'
        }[state]
        if (!ret) return ''
        return ret
      },
      imgType (devType) {
        let ret = {
          'Смартфоны': 'smartphone',
          'Планшетные компьютеры': 'tablet',
          'Ноутбуки': 'laptop',
          'Digital Signage': 'dashboard'
        }[devType]
        if (!ret) return ''
        return ret
      }

    }
  }
</script>



