
<template>
<!--v-select
            :loading="loading"
            :items="items"
            :rules="[() => select.length > 0 || 'Введите код магазина']"
            :search-input.sync="search"
            v-model="valueProxy"
            label="Код магазина"
            autocomplete
      
            cache-items
    
            required
-->
<v-select
          :items="items"
          v-model="valueProxy"
          label="Код магазина"
          item-text="code"
          item-value="code"
          :search-input.sync="search"
          :filter="customFilter"
          

          max-height="auto"
         
          autocomplete
        >

          <template slot="item" slot-scope="data">
            <template v-if="typeof data.item !== 'object'">
              <v-list-tile-content v-text="data.item"></v-list-tile-content>
            </template>
            <template v-else>
              <v-list-tile-content>
                <v-list-tile-title >  {{ data.item.code }}</v-list-tile-title>
                
                <v-list-tile-sub-title v-html="data.item.name"></v-list-tile-sub-title>
              </v-list-tile-content>
            </template>
          </template>
</v-select>
</template>

<script>
export default {
  name: 'showcase-selector',
  props: {
    value: {
      required: true
    }
  },
  computed: {
    valueProxy: {
      get () {
        return this.value
      },
      set (value) {
        this.$emit('input', value)
      }
    }
  },
  data () {
    return {
      loading: false,
      items: [
        {
          code: 'S526',
          name: 'м. «Выставочная», ТРЦ «АФИМОЛЛ Сити»',
          address: 'Москва, Пресненская наб, д.2, ТРЦ «АФИМОЛЛ Сити»'
        }
      ],
      search: null,
      select: [],

      shops: [
        {
          code: 'S526',
          name: 'м. «Выставочная», ТРЦ «АФИМОЛЛ Сити»',
          address: 'Москва, Пресненская наб, д.2, ТРЦ «АФИМОЛЛ Сити»'
        },
        {
          code: 'S731',
          name: 'м. «Фили», ТРЦ «Филион»',
          address: 'Москва, Багратионовский проезд, д. 5, ТРЦ «Филион»'
        },
        {
          code: 'S064',
          name: '24 часа! м. «Улица 1905 года»',
          address: 'Москва, ул. Красная Пресня, д. 23, к. Б, стр. 1'
        }
      ]
    }
  },
  watch: {
    search (val) {
      val && this.querySelections(val)
    }
  },
  methods: {
    querySelections (v) {
      this.loading = true
      // Simulated ajax query
      setTimeout(() => {
        this.items = this.shops.filter(e => {
          return (Object.values(e).join('') || '')
            .toLowerCase()
            .indexOf((v || '').toLowerCase()) > -1
        })
        this.loading = false
      }, 500)
    },
    customFilter (item, queryText, itemText) {
      const hasValue = val => val != null ? val : ''
      const text = hasValue(Object.values(item).join(''))
      const query = hasValue(queryText)
      return text.toString()
        .toLowerCase()
        .indexOf(query.toString().toLowerCase()) > -1
    }
  }
}
</script>



