// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import {
  Vuetify,
  VApp,
  VNavigationDrawer,
  VFooter,
  VList,
  VBtn,
  VIcon,
  VGrid,
  VToolbar,
  VCard,
  VStepper,
  VDivider,
  VSubheader,
  VSelect,
  VChip,
  VAvatar,
  VMenu,
  VTextField,
  VDatePicker,
  VRadioGroup,
  VTabs,
  VDataTable,
  VDataIterator,
  VTooltip,
  VAlert,
  transitions
} from 'vuetify'
import '../node_modules/vuetify/src/stylus/app.styl'
import VueVega from 'vue-vega'
Vue.use(VueVega)
Vue.use(Vuetify, {
  components: {
    VApp,
    VNavigationDrawer,
    VFooter,
    VStepper,
    VList,
    VBtn,
    VIcon,
    VGrid,
    VToolbar,
    VCard,
    VDivider,
    VSubheader,
    VSelect,
    VChip,
    VAvatar,
    VMenu,
    VTextField,
    VDatePicker,
    VRadioGroup,
    VTabs,
    VDataTable,
    VDataIterator,
    VTooltip,
    VAlert,
    transitions
  },
  theme: {
    primary: '#124BCF',
    secondary: '#494949',
    accent: '#7800D7',
    error: '#B10010',
    info: '#124BCF',
    success: '#1AC326',
    warning: '#FDAE09'
  }
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
})



