<template>
  <v-app>
    <v-navigation-drawer persistent :mini-variant="miniVariant" :clipped="clipped" v-model="drawer" enable-resize-watcher fixed app>
      <config-view></config-view>
    </v-navigation-drawer>
    <v-toolbar app dense tabs color="light-gray">
      <v-toolbar-side-icon @click.stop="drawer = !drawer">
        <v-icon>settings</v-icon>
      </v-toolbar-side-icon>
      <v-toolbar-title>Отчет о магазине </v-toolbar-title>
      <!--v-btn  icon outline color="primary" to ="/dashboard" >
          <v-icon>dashboard</v-icon>
        </v-btn>
        <p>Обзор</p>
        <v-divider ></v-divider>

        <v-btn icon  outline color="primary" to ="/devicelist">
          <v-icon >filter_list</v-icon>
        </v-btn>

        <v-divider ></v-divider>

        <v-btn  outline  icon color="primary" disabled to ="/deviceitem">
          <v-icon outline>description</v-icon>
        </v-btn-->
      <v-tabs slot="extension" color="grey lighten-4">
        <v-tab to="/dashboard" prev-icon>
          <v-icon color="primary ">dashboard</v-icon>Обзор
          <v-icon>arrow_right</v-icon>
        </v-tab>
        <v-tab to="/devicelist" prev-icon>
          <v-icon color="primary">filter_list</v-icon> Устройства
          <v-icon>arrow_right</v-icon>
        </v-tab>
        <v-tab disabled to="/deviceitem" prev-icon>
          <v-icon outline>description</v-icon> Детали
        </v-tab>

      </v-tabs>

      <!--v-stepper v-model="stepNumber " slot="extension " >
        <v-stepper-header >
          <v-stepper-step step="1 " editable :complete="stepNumber> 1" >Обзор</v-stepper-step>
        <v-divider></v-divider>
        <v-spacer></v-spacer>
        <v-stepper-step step="2" editable :complete="stepNumber > 2" to="/devicelist">Устройства</v-stepper-step>
        <v-divider></v-divider>
        <v-spacer></v-spacer>
        <v-stepper-step step="3">Детали</v-stepper-step>
        </v-stepper-header>
        </v-stepper-->

    </v-toolbar>
    <v-content>
      <router-view/>
    </v-content>
    <v-navigation-drawer temporary :right="right" v-model="rightDrawer" fixed app>
      <v-list>
        <v-list-tile @click="right = !right">
          <v-list-tile-action>
            <v-icon>compare_arrows</v-icon>
          </v-list-tile-action>
          <v-list-tile-title>Switch drawer (click me)</v-list-tile-title>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    <v-footer :fixed="false" app>
      <span class="mx-2">&copy; RetailRotor, 2018</span>
    </v-footer>
  </v-app>
</template>

<script>
import Vue from 'vue'
import VueVega from 'vue-vega'
import ConfigView from '@/components/ConfigView'

Vue.component(ConfigView.name, ConfigView)
Vue.use(VueVega)

export default {
  data () {
    return {
      clipped: false,
      drawer: false,
      fixed: false,
      stepNumber: 1,

      miniVariant: false,
      right: true,
      rightDrawer: false,
      title: 'Vuetify.js'
    }
  },
  name: 'App'
}
</script>



