<template>
  <v-container fluid grid-list-md>
    <v-slide-y-transition mode="out-in">

      <v-layout row wrap>
        <v-flex d-flex xs12>
          <v-card>
            <v-card-title primary-title>
              <v-layout row wrap>
                <v-flex d-flex xs12 md7>
                  <div class="headline">Отчет по магазину
                    <strong>S526</strong> на
                    <strong>25.04.2018</strong>
                  </div>
                </v-flex>
                <v-flex d-flex xs12 md5>
                  <div class="body-2">
                    м. «Выставочная», ТРЦ «АФИМОЛЛ Сити», m_mobile
                    <br>
                    <span class="caption">Москва, Пресненская наб, д.2, ТРЦ «АФИМОЛЛ Сити» </span>
                  </div>
                </v-flex>
              </v-layout>
            </v-card-title>
          </v-card>
        </v-flex>

        <v-flex d-flex md4 xs12>
          <v-card>
            <v-card-title primary-title>
              <div class="title">Текущие статусы устройств</div>
            </v-card-title>
            <v-card-media>
              <vega-chart :spec="chart_by_state" class="mx-auto">
              </vega-chart>

            </v-card-media>
            <v-card-text>

            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex d-flex md8>
          <v-card>
            <v-card-title primary-title>
              <div class="title">История работы устройств</div>
            </v-card-title>
            <v-card-media>
              <vega-chart :spec="chart_by_history" class="mx-auto">
              </vega-chart>

            </v-card-media>
            <v-card-text>

            </v-card-text>
          </v-card>
        </v-flex>

        <v-flex d-flex md6>
          <v-card>
            <v-card-title primary-title>
              <div class="title">Статусы по группам</div>
            </v-card-title>
            <v-card-media>
              <vega-chart :spec="chart_by_group" class="mx-auto">
              </vega-chart>

            </v-card-media>
            <v-card-text>

            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex d-flex md6>
          <v-card>
            <v-card-title primary-title>
              <div class="title">Текущие ошибки</div>
            </v-card-title>
            <v-card-media class="mx-auto">
              <vega-chart :spec="chart_by_error" class="mx-auto">
              </vega-chart>

            </v-card-media>
            <v-card-text>

            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex v-for="group in data.groups" :key="group.id">
          <v-layout row wrap>
            <v-flex d-flex xs11 offset-xs1>
              <v-subheader> {{group.name}}</v-subheader>
            </v-flex>
            <v-flex d-flex md3 xs12>
              <v-card>
                <v-card-title primary-title>
                  <div class="title">Cтатусы устройств<br>
                    <span class="caption">{{group.name}}</span>
                  </div>
                </v-card-title>
                <v-card-media>
                  <vega-chart :spec="chart_by_state" class="mx-auto">
                  </vega-chart>

                </v-card-media>
                <v-card-text>

                </v-card-text>
              </v-card>
            </v-flex>
            <v-flex d-flex xs12 md6>
              <v-card>
                <v-card-title primary-title>
                  <div class="title">История работы устройств<br>
                    <span class="caption">{{group.name}}</span>
                  </div>
                </v-card-title>
                <v-card-media>
                  <vega-chart :spec="chart_by_history" class="mx-auto">
                  </vega-chart>

                </v-card-media>
                <v-card-text>

                </v-card-text>
              </v-card>
            </v-flex>
            <v-flex d-flex xs12 md3>
              <v-card>
                <v-card-title primary-title>
                  <div class="title">Ошибки<br>
                    <span class="caption">{{group.name}}</span>
                  </div>
                </v-card-title>
                <v-card-media>
                  <vega-chart :spec="require('@/assets/charts/group_error.vg.json')" class="mx-auto">
                  </vega-chart>

                </v-card-media>
                <v-card-text>

                </v-card-text>
              </v-card>
            </v-flex>

          </v-layout>
        </v-flex>
        <template v-if="false">
          <v-flex d-flex md6>
            <v-card>
              <v-card-title primary-title>
                <div class="title">Статус по категориям</div>
              </v-card-title>
              <v-card-media>
                <vega-chart :spec="spec_by_group">
                </vega-chart>

              </v-card-media>
              <v-card-text>
                Щелкните по интресующей части графика, чтобы посмотреть устройства
              </v-card-text>
            </v-card>
          </v-flex>
          <v-flex d-flex md6>
            <v-card>
              <v-card-title primary-title>
                <div class="title">Статус по состояниям</div>
              </v-card-title>
              <v-card-media>
                <vega-chart :spec="spec_by_state">
                </vega-chart>

              </v-card-media>
              <v-card-text>
                Щелкните по интресующей части графика, чтобы посмотреть устройства
              </v-card-text>
            </v-card>
          </v-flex>

          <v-flex d-flex>
            <v-card>
              <v-card-title primary-title>
                <div class="headline">История обновления устройсв</div>
              </v-card-title>
              <v-card-media>
                <vega-chart :spec="spec">
                </vega-chart>

              </v-card-media>
              <v-card-text>
                Щелкните по интресующей части графика, чтобы посмотреть устройства
              </v-card-text>
            </v-card>
          </v-flex>
        </template>
      </v-layout>
      </v-flex>
      </v-layout>
    </v-slide-y-transition>
  </v-container>
</template>


<script>
  import Vue from 'vue'
  import VueVega from 'vue-vega'
  import VegaEmbed from '@/components/VegaEmbed.vue'
  import Vega from '@/components/Vega.vue'
  import spec1 from '@/assets/charts/spec1.vg.json'
  import ByGroup from '@/assets/charts/by_group.vg.json'
  import ByState from '@/assets/charts/by_state.vg.json'
  import ByState2 from '@/assets/charts/by_state2.vg.json'
  import ByError from '@/assets/charts/by_error.vg.json'
  import ByGroup2 from '@/assets/charts/by_group2.vg.json'
  import AllHistory from '@/assets/charts/all_dev_history.vg.json'

  Vue.use(VueVega)
  Vue.component(VegaEmbed.name, VegaEmbed)
  Vue.component(Vega.name, Vega)

  export default {
    data: () => ({
      chart_by_state: ByState2,
      chart_by_error: ByError,
      chart_by_group: ByGroup2,
      chart_by_history: AllHistory,

      spec_by_group: ByGroup,
      spec_by_state: ByState,
      spec: spec1,
      data: {
        groups: [
          {name: 'Digital Signage', id: 1},
          {name: 'Ноутбуки', id: 2},
          {name: 'Планшетные компьютеры', id: 3},
          {name: 'Смартфоны', id: 4}
        ]
      }
    })
  }
</script>



