<template>
  <v-list>
    <v-subheader>Параметры отчета</v-subheader>
    <v-list-tile>
      <showcase-selector v-model="showcaseCode"></showcase-selector>
    </v-list-tile>
    <v-radio-group v-model="reportType" :mandatory="false">
      <v-list-tile>
        <v-radio label="Послений отчет" value="Current"></v-radio>
      </v-list-tile>
      <v-list-tile>
        <v-radio label="Отчет на дату" value="Date"></v-radio>
      </v-list-tile>
      <v-list-tile>
        <v-radio label="Отчет по ссылке" value="Code"></v-radio>
      </v-list-tile>
    </v-radio-group>

    <v-list-tile v-show="reportType==='Date'">
      <v-menu ref="dateMenu" :close-on-content-click="false" v-model="dateMenu" :nudge-right="0" :return-value.sync="date" lazy transition="scale-transition" offset-y full-width min-width="200px">
        <v-text-field slot="activator" v-model="date" label="Датa отчета" prepend-icon="event"></v-text-field>
        <v-date-picker v-model="date" @input="$refs.dateMenu.save(date)" no-title>
        </v-date-picker>
      </v-menu>
    </v-list-tile>
    <v-list-tile v-show="reportType==='Code'">
      <v-text-field v-model="reportCode" label="Код отчета"></v-text-field>
    </v-list-tile>
    <v-list-tile>
      <v-btn flat>Отменить</v-btn>
      <v-btn color="primary">Применить</v-btn>
    </v-list-tile>

  </v-list>
</template>

<script>
import Vue from 'vue'
import ShowcaseSelector from '@/components/ShowcaseSelector'
Vue.component(ShowcaseSelector.name, ShowcaseSelector)

export default {
  name: 'config-view',
  data () {
    return {
      showcaseCode: 'S526',
      reportCode: '',
      date: null,
      reportType: 'Current',
      dateMenu: false
    }
  }
}
</script>




