
<template>
  <div id="vis">
    <p>Моя Заглужка</p>
  </div>
</template>

<script>
//  import VueVega from 'vue-vega'
// import * as vg from 'vega'
// import * as vl from 'vega-lite'
import ve from 'vega-embed'

export default {
  name: 'vega-embed',

  mounted: function () {
    this.view = this.createView(this.spec)
  },
  watch: {
    spec: {
      handler: function () {
        this.view = this.createView(this.spec)
      },
      deep: true
    }
  },
  methods: {
    createView: function (spec) {
      var tspec = 'https://raw.githubusercontent.com/vega/vega/master/docs/examples/bar-chart.vg.json'
      ve.wrapper('#vis', tspec).then(function (result) {
        // access view as result.view
      }).catch(console.error)
    }
  }
}
</script>



